<template>
  <div>
    <div class="createapply-formgroup-title">
      <em v-if="item.is_must==true" class="is-must">*</em> {{item.name}}
    </div>
    <div class="createapply-formgroup">
      <div class="createapply-formgroup-content createapply-formgroup-content-flex">
        <div class="createapply-formgroup-content-flex-title"><em v-if="item.is_must_fund==true" class="is-must">*</em>{{item.name1}}：</div>
        <div class="select-normal-36">
          <el-select v-model="value_fund" :multiple="item.is_multiple" filterable clearable :placeholder="item.placeholder" @change="getSelectdata(0)">
            <el-option v-for="(option,oindex) in fundList" :key="oindex" :label="option.name" :value="option.id">
            </el-option>
          </el-select>
        </div>
      </div>
      <div class="createapply-formgroup-content createapply-formgroup-content-flex">
        <div class="createapply-formgroup-content-flex-title"><em v-if="item.is_must_pro==true" class="is-must">*</em>{{item.name2}}：</div>
        <div class="select-normal-36">
          <el-select v-model="value_pro" :multiple="item.is_multiple" filterable clearable :placeholder="item.placeholder" @change="getSelectdata(1)">
            <el-option v-for="(option,oindex) in proList" :key="oindex" :label="option.name" :value="option.id">
            </el-option>
          </el-select>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Bus from '../../config/bus'
import { getFundlist, getFundproject } from '../../config/api'
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
export default {
  data() {
    return {
      fundList: [],
      proList: [],
      value_fund: '',
      value_pro: '',
    }
  },
  components: {},
  props: ['item'],
  created() {
    if (this.item.value[0] != undefined && this.item.value[0].id != undefined) {
      this.value_fund = this.item.value[0].id;
    }
    if (this.item.value[1] != undefined && this.item.value[1].id != undefined) {
      this.value_pro = this.item.value[1].id;
    }
    this.getfund();
  },
  computed: {},
  mounted() {

  },
  methods: {
    getSelectdata(index) {
      //index0=>基金 1=>项目
      if (index == 0) {
        let newArr  = this.fundList.filter(item => {
          return this.value_fund == item.id
        });
        this.item.value[index] = newArr[0];
        this.getFundproject(true);
      } else {
        let newArr = this.proList.filter(item => {
          return this.value_pro == item.id
        });
        this.item.value[index] = newArr[0];
        if (this.item.is_fresh === true) {
          Bus.$emit('freshApprover', 3, this.value_pro, this.value_fund)
        }
      }
      
    },
    getfund() {
      getFundlist().then(res => {
        if (res.data.msgNo == 1) {
          this.fundList = res.data.payload;
          if (this.fundList[0] != undefined) {
            this.getFundproject(false);
          } else {
            this.value_fund = ''
            this.item.value[0] = ''
            this.value_pro = '';
            this.item.value[1] = '';
          }
        }
      })
    },
    getFundproject(fresh) {
      if (!this.value_fund == undefined) {
        return;
      }
      getFundproject({
        fund_id: this.value_fund
      }).then(res => {
        if (res.data.msgNo == 1) {
          this.proList = res.data.payload;
          // this.value_pro = '';
          // this.item.value[1] = '';
          if (this.proList[0] != undefined) {
            // if (!this.value_pro) {
            //   // this.value_pro = this.proList[0].id;
            //   // this.item.value[1] = this.proList[0];
            // }
            if (fresh) {
              this.value_pro = this.proList[0].id;
              this.item.value[1] = this.proList[0];
              if (this.item.is_fresh === true) {
                Bus.$emit('freshApprover', 3, this.value_pro, this.value_fund)
              }
            }
          } else {
            this.value_pro = '';
           this.item.value[1] = '';
          }
        }
      })
    },
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/apply';

.fund-items {
  justify-content: space-between;
}

</style>



