<template>
  <div class="">
    <div class="createapply-formgroup-title">
      <em v-if="item.is_must==1" class="is-must">*</em> {{item.name}}
    </div>
    <div class="daterange-box">
      <div class="daterange-group">
        <div class="daterange-group-title">{{item.name1}}:</div>
        <div>
          <date-picker v-if="item.format=='yyyy-MM-dd HH:mm'||item.format=='yyyy-MM-dd HH:mm:ss'" class="date_picker_normal" :disabled="item.is_disabled===false" :date="date" :option="item.format=='yyyy-MM-dd HH:mm'?option:option1" @change="getDate($event,0)"></date-picker>
          <el-date-picker :disabled="item.is_disabled===false" v-if="item.format=='yyyy'" v-model="item.value[0]"  :editable='false' type="year" placeholder="选择年" @change="checkDate(0)" :value-format="item.format">
          </el-date-picker>
          <el-date-picker :disabled="item.is_disabled===false" v-if="item.format=='yyyy-MM'" v-model="item.value[0]" :editable='false' type="month" placeholder="选择月" @change="checkDate(0)" :value-format="item.format">
          </el-date-picker>
          <el-date-picker :disabled="item.is_disabled===false" v-if="item.format=='yyyy-MM-dd'" v-model="item.value[0]" :editable='false'  type="date" placeholder="选择日期" @change="checkDate(0)" :value-format="item.format">
          </el-date-picker>
        </div>
      </div>
      <div class="daterange-group">
        <div class="daterange-group-title">{{item.name2}}:</div>
        <div>
          <date-picker v-if="item.format=='yyyy-MM-dd HH:mm'||item.format=='yyyy-MM-dd HH:mm:ss'" class="date_picker_normal" :disabled="item.is_disabled===false" :date="date1" :option="item.format=='yyyy-MM-dd HH:mm'?option:option1" @change="getDate($event,1)"></date-picker>
          <el-date-picker :disabled="item.is_disabled===false" :editable='false' v-if="item.format=='yyyy'" v-model="item.value[1]" type="year" placeholder="选择年" @change="checkDate(1)" :value-format="item.format">
          </el-date-picker>
          <el-date-picker :disabled="item.is_disabled===false" :editable='false' v-if="item.format=='yyyy-MM'" v-model="item.value[1]" type="month" placeholder="选择月" @change="checkDate(1)" :value-format="item.format">
          </el-date-picker>
          <el-date-picker :disabled="item.is_disabled===false" :editable='false' v-if="item.format=='yyyy-MM-dd'" v-model="item.value[1]" type="date" placeholder="选择日期" @change="checkDate(1)" :value-format="item.format">
          </el-date-picker>
        </div>
      </div>
      <div class="daterange-group">
        <div class="daterange-group-title">{{item.name3}}:</div>
        <div class="daterange-group-content">
          <el-input :disabled="true" placeholder="" v-model="item.value[2]" class="" :clearable='true'></el-input>
          <span>{{unitlist[item.scal_unit]}}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import myDatepicker from 'vue-datepicker/vue-datepicker-es6-copy.vue'
  import Bus from '../../config/bus'
  import {
    dialogConfirm,
    dialogConfirmHasbtn
  } from '../../config/mUtils'
  import {
    calcTotalTimeNew
  } from '../../config/api'
  export default {
    data() {
      return {
        unitlist: {
          year: '时',
          month: '月',
          day: '天',
          hour: '时',
          min: '时',
          sec: '时',
          work: '时'
        },
        date: {
          time: ''
        },
        date1: {
          time: ''
        },
        //时间选择器配置
        option: {
          type: 'min',
          week: ['一', '二', '三', '四', '五', '六', '日'],
          month: ['一月', '二月', '三月', '四月', '五月', '六月', '七月', '八月', '九月', '十月', '十一月', '十二月'],
          format: 'YYYY-MM-DD HH:mm',
          placeholder: '请选择',
          overlayOpacity: 0.5, // 0.5 as default
          dismissible: true // as true as default
        },
        option1: {
          type: 'sec',
          week: ['一', '二', '三', '四', '五', '六', '日'],
          month: ['一月', '二月', '三月', '四月', '五月', '六月', '七月', '八月', '九月', '十月', '十一月', '十二月'],
          format: 'YYYY-MM-DD HH:mm:ss',
          placeholder: '请选择',
          overlayOpacity: 0.5, // 0.5 as default
          dismissible: true // as true as default
        },
      }
    },
    components: {
      'date-picker': myDatepicker,
    },
    props: ['item', 'type'],
    created() {
      for (let i = 0; i < 3; i++) {
        if (this.item.value[i] == undefined || this.item.value[i] == null) {
          this.item.value[i] = '';
        }
      }
      this.date.time = this.item.value[0]
      this.date1.time = this.item.value[1]
    },
    computed: {},
    mounted() {},
    methods: {
      getDate(date, index) {
        this.item.value[index] = date;
        this.checkDate(index, true);
      },
      checkDate(index, canequal) {
        let s_time = this.item.value[0];
        let e_time = this.item.value[1];
        if (!s_time || !e_time) {
          this.item.value[2] = '';
          this.undateData();
        } else {
          s_time = Date.parse(new Date(s_time)) / 1000;
          e_time = Date.parse(new Date(e_time)) / 1000;

          //判断按天选择可以相等，按时选择不可以相等
          if (canequal) {
            if (s_time >= e_time) {
              dialogConfirm('info', '开始时间不能大于等于结束时间');
              this.item.value[index] = '';
              this.item.value[2] = '';
              if (index == 0) {
                this.date.time = ''
              } else {
                this.date1.time = ''
              }
              this.undateData();
            } else {
              let params = {
                start_time: s_time,
                end_time: e_time,
                scal_unit: this.item.scal_unit,
              }
              // if(params.scal_unit=='hour'){
              //   params.typ='work'
              // }
              calcTotalTimeNew(params).then(res => {
                if (res.data.msgNo == 1) {
                  let time = res.data.payload;
                  if (!this.type) {
                    this.item.value[2] = res.data.payload;
                  } else {
                    let resttime = parseInt(this.type.hours);
                    if (this.type.status == 1) {
                      if (time > resttime) {
                        dialogConfirm('info', '你的假期不足');
                        this.item.value = ['', '', ''];
                        this.date.time = '';
                        this.date1.time = '';
                      } else {
                        this.item.value[2] = res.data.payload;
                      }
                    } else {
                      this.item.value[2] = res.data.payload;
                    }
                  }
                  this.undateData();
                }
              })
            }
          } else {
            if (s_time > e_time) {
              dialogConfirm('info', '开始时间不能大于结束时间');
              this.item.value[index] = '';
              this.item.value[2] = '';
              if (index == 0) {
                this.date.time = ''
              } else {
                this.date1.time = ''
              }
              this.undateData();
            } else {
              let params = {
                start_time: s_time,
                end_time: e_time,
                scal_unit: this.item.scal_unit,
              }
              // if(params.scal_unit=='hour'){
              //   params.typ='work'
              // }
              calcTotalTimeNew(params).then(res => {
                if (res.data.msgNo == 1) {
                  let time = res.data.payload;
                  if (!this.type) {
                    this.item.value[2] = res.data.payload;
                  } else {
                    let resttime = parseInt(this.type.hours);
                    if (this.type.status == 1) {
                      if (time > resttime) {
                        dialogConfirm('info', '你的假期不足');
                        this.item.value = ['', '', ''];
                        this.date.time = '';
                        this.date1.time = '';
                      } else {
                        this.item.value[2] = res.data.payload;
                      }
                    } else {
                      this.item.value[2] = res.data.payload;
                    }
                  }
                  this.undateData();
                }
              })
            }
          }
  
        }
      },
      undateData() {
        let value = this.item.value;
        this.item.value = [];
        value.forEach(item => {
          this.item.value.push(item);
        })
        this.$emit('change', this.item.value);
      },
    }
  }
</script>

<style lang="scss" scoped>
  @import '../../style/apply';
  .fund-items {
    justify-content: space-between;
  }
</style>



