<template>
  <div>
    <div v-for="(child,index) in item.children">
      <div v-if="child.fields=='foreignSeal_foreignSelect_fund'" class="createapply-formgroup">
        <data-select :item="child"></data-select>
      </div>
      <div v-if="child.fields=='foreignSeal_projectSelect_project'" class="createapply-formgroup">
        <data-select :item="child"></data-select>
      </div>
      <div class="createapply-formgroup" v-if="child.fields=='foreignSeal_attachment_file'">
        <upload-file :item="child" type="file"></upload-file>
      </div>
      <!-- 横向页面单选 -->
      <div class="createapply-formgroup" v-if="child.fields=='foreignSeal_radio_sms'">
        <radio-select :item="child"></radio-select>
      </div>
    </div>
  </div>
</template>
<script>
import uploadFile from '../applyComponent/uploadFile.vue'
import dataSelect from '../applyComponent/dataSelect.vue'
import radioSelect from '../applyComponent/radioSelect.vue'
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
export default {
  data() {
    return {}
  },
  components: {
    uploadFile,
    dataSelect,
    radioSelect
  },
  props: ['item'],
  created() {},
  computed: {},
  mounted() {

  },
  methods: {
    getDate(date) {
      this.item.value = date;
    },
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/apply';
.fund-items {
  justify-content: space-between;
}

</style>



