<template>
  <div>
    <div class="createapply-formgroup-content">
      <div class="formgroup-info-box">
        <div>{{item.name}}</div>
        <em class="" @click="linkClick">{{item.linkText}}</em>
      </div>
    </div>
    <!-- 假期弹窗 -->
    <!-- 移动文件弹窗 -->
    <el-dialog title="我的假期" :visible.sync="showMyvacation" width="570px" center :append-to-body='true' class="dialog-content-vacation applyfun-modal">
      <div class="dialog_content">
        <div class="vacation-link" @click="gotoApplylist">查看全部请假记录<i class="el-icon-arrow-right"></i></div>
        <div class="vacationlist">
          <div class="vacationitem" v-for="item in myVacation">
            <div class="vacationitem-name">{{item.name}}</div>
            <div class="vacationitem-time"><em v-if="item.status==0">已请</em><em v-if="item.status==1">剩余</em>{{item.hours}}小时</div>
          </div>
        </div>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
import { getMyholidays } from '../../config/api'
export default {
  data() {
    return {
      //我的假期弹窗
      showMyvacation: false,
      myVacation: [],
    }
  },
  components: {},
  props: ['item'],
  created() {},
  computed: {},
  mounted() {
  },
  methods: {
    linkClick() {
      let type = parseInt(this.item.skip[0].skip_type);
      let site = parseInt(this.item.skip[0].skip_site);
      switch (type) {
        case 1:
          this.showDialog(site);
          break;
        case 4:
          this.gotoLink(site);
          break;
      }
    },
    //显示弹窗
    showDialog(site) {
      if (site == 5) {
        this.showVacation();
      }
    },
    showVacation(){
      this.showMyvacation = true;
      if (this.myVacation.length == 0) {
        //获取假期
        getMyholidays('').then(res => {
          if (res.data.msgNo == 1) {
            this.myVacation = res.data.payload;
          }
        })
      }
    },
    //前往请假记录列表
    gotoApplylist() {
      let newObj = JSON.parse(JSON.stringify(this.$route.query))
      newObj.status = 3;
      this.$router.push({
        name: 'applyListNew',
        query: newObj
      })
    },
    //跳转链接
    gotoLink(site) {
      window.open(site, '_blank');
    },
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/apply';

.fund-items {
  justify-content: space-between;
}

</style>



