<template>
  <div class="createapply-formgroup-content">
    <div class="formgroup-approverList">
      <div class="formgroup-approverItem" v-for="item in data">
        <div class="approverItem-icon-check">
          <!-- <img v-if="item.status==0" src="../../images/apply_approver.png"> -->
          <span></span>
        </div>
        <div class="approverItem-text">
          {{item.position}}
        </div>
        <div class="approverItem-namelist">
          <div class="approverItem-nameitem" v-for="user in item.users">
            <img @error="getImgurl" :src="user.url" alt="">
            <span>{{user.name}}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {}
  },
  mounted() {

  },
  created() {},
  computed: {},
  props: ['data'],
  methods: {
    getImgurl(e) {
      e.currentTarget.src = '../../static/images/header_userimg.png';
      e.onerror = null;
    }
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/mixin';
@import '../../style/apply';
.createapply-formgroup-content {
  margin-left: 0px;
}

</style>



