<template>
  <div class="createapply-formgroup-content">
    <div class="createapply-formgroup-content">
      <div class="formgroup-selectedbox formgroup-selectedbox-hasimg clear">
        <div class="formgroup-selecteditem-hasimg" v-for="(item,index) in selectList">
          <div class=""><img :src="item.url" @error="getImgurl">{{item.name}}</div>
          <img src="../../images/apply_icon_delete.png" class="icon-delete" v-if="defaultList.indexOf(item.id)==-1" @click="deleteSelectedItem(item.id,index)">
        </div>
      </div>
    </div>
    <div class="createapply-formgroup-content">
      <div class="select-normal-36">
        <el-select v-model="value" filterable clearable placeholder="选择抄送人" @change="getSelect">
          <el-option-group v-for="(option,oindex) in options" :key="oindex" :label="option.name">
            <el-option v-if="selectId.indexOf(child.id)==-1" v-for="(child,cindex) in option.children" :key="cindex" :label="child.name" :value="child.id">
            </el-option>
          </el-option-group>
        </el-select>
      </div>
    </div>
  </div>
</template>
<script>
import Bus from '../../config/bus'
import { getUserNew } from '../../config/api'
export default {
  data() {
    return {
      selectId:[],//已选人员id列表
      selectList: [], //已选人员列表
      dataList: [], //人员列表
      defaultList: [], //默认人员id列表
      options: [], //带部门的人员列表
      value: ''
    }
  },
  mounted() {},
  created() {
    this.getData();
  },
  computed: {},
  props: ['data', 'default'],
  methods: {
    //初始化已选中的成员
    initSelect() {
      this.defaultList = [];
      this.selectList = [];
      this.default.forEach(i => {
        this.defaultList.push(parseInt(i));
      })
      this.data.forEach(item => {
        this.selectList.push(item);
      })
      this.updateOptions();
    },
    //清除已选中的选项，让其不可选择
    updateOptions() {
      this.selectId = [];
      this.selectList.forEach(s => {
        this.selectId.push(s.id);
      })
      Bus.$emit('getCopier',this.selectList);
    },
    //获取人员列表
    getData() {
      getUserNew().then(res => {
        if (res.data.msgNo == 1) {
          this.options = res.data.payload;
          let dataList = [];
          res.data.payload.forEach(item => {
            if (item.children != undefined) {
              dataList = dataList.concat(item.children);
            }
          })
          this.dataList = dataList;
          this.initSelect();
        }
      })
    },
    //点击下拉选项选择人员
    getSelect() {
      let item = this.dataList.filter(item => {
        return item.id == this.value
      })[0]
      this.selectList.push(item);
      this.updateOptions();
      this.value = '';
    },
    //删除人员
    deleteSelectedItem(id,index){
      this.selectList.splice(index,1);
      this.updateOptions();
    },
    getImgurl(e) {
      e.currentTarget.src = '../../static/images/header_userimg.png';
      e.onerror = null;
    }
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/mixin';
@import '../../style/apply';
.createapply-formgroup-content {
  margin-left: 0px;
}

</style>



