<template>
  <div>
    <div v-for="child in curitem.children">
      <div class="" v-if="child.fields=='reimbursement_grouplist_ordinary'">
        <div class="createapply-groupbox" v-for="(grouplist,grouplistindex) in child.children">
          <div class="createapply-groupbox-delect">
            <span class="iconfont icon-guanbi" @click="deleteChild(child,grouplistindex)"></span>
          </div>
          <div class="" v-for="(group,groupindex) in grouplist.children">
            <div v-if="group.fields=='reimbursement_grouplist_group_textNote_ordinary'">
              <div class="createapply-formgroup-content">
                <info-area :item="group"></info-area>
              </div>
            </div>
            <!-- 金额 -->
            <div class="createapply-formgroup" v-if="group.fields=='reimbursement_grouplist_group_money_ordinary'">
              <money-input :item="group" @change="updateTotal"></money-input>
            </div>
            <!-- 单选框 -->
            <div class="createapply-formgroup" v-if="group.fields=='reimbursement_grouplist_group_select_ordinary'">
              <select-normal :item="group"></select-normal>
            </div>
            <!-- 多行输入框  -->
            <div class="createapply-formgroup" v-if="group.fields=='reimbursement_grouplist_group_textarea_ordinary'">
              <input-normal :item="group"></input-normal>
            </div>
          </div>
        </div>
      </div>
      <!-- 说明文字 -->
      <div v-if="child.fields=='reimbursement_actionText_ordinary'" class="createapply-formgroup">
        <div class="createapply-formgroup-content text_center">
          <div class="formgroup-action-box clear" @click="createGroup">
            {{child.name}}
          </div>
        </div>
      </div>
      <!-- 数字输入框 -->
      <div class="createapply-formgroup" v-if="child.fields=='reimbursement_money_total'">
        <money-input :item="child"></money-input>
      </div>
    </div>
  </div>
</template>
<script>
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
import inputNormal from '../applyComponent/inputNormal.vue'
import selectNormal from '../applyComponent/selectNormal.vue'
import moneyInput from '../applyComponent/moneyInput.vue'
import infoArea from '../applyComponent/infoArea.vue'
export default {
  data() {
    return {
      curitem:'',
      group:''
    }
  },
  components: {
    inputNormal,
    moneyInput,
    selectNormal,
    infoArea
  },
  props: ['item'],
  created() {
    this.curitem=this.item;
    this.curitem.children.forEach(item => {
      if (item.fields == 'reimbursement_grouplist_ordinary') {
        this.group = JSON.parse(JSON.stringify(item.children[0]));
      }
    })
    this.item.children=this.curitem.children;
  },
  computed: {},
  mounted() {

  },
  methods: {
    createGroup() {
      this.curitem.children.forEach(item => {
        if (item.fields == 'reimbursement_grouplist_ordinary') {
          let group=JSON.parse(JSON.stringify(this.group));
          group.children.forEach(child=>{
            if(child.fields=="reimbursement_grouplist_group_textNote_ordinary"){
              child.name='报销明细'+(item.children.length+1)
            }
          })
          item.children.push(group);
        }
      })
      this.item.children=this.curitem.children;
    },
    updateTotal(){
      let num = 0;
      this.curitem.children.forEach(item => {
        if (item.fields == 'reimbursement_grouplist_ordinary') {
          let children = item.children
          children.forEach(child => {
            child.children.forEach(group => {
              if (group.fields == 'reimbursement_grouplist_group_money_ordinary') {
                if (group.value[0] != undefined || group.value[0] != '') {
                  num += parseInt(group.value[0]);
                }
              }
            })
          })
        }
      })
      this.curitem.children.forEach(item => {
        if (item.fields == 'reimbursement_money_total') {
          item.value = [];
          item.value.push(num);
        }
      })
    },
    deleteChild(list, index) {
      if (list.is_delete === true) {
        if (list.children.length == 1) {
          dialogConfirm('info', '至少要有一个明细');
          return;
        }
        dialogConfirmHasbtn('确定要删除该行程吗？').then(res => {
          list.children.splice(index, 1);
          list.children.forEach((group,index) => {
            group.children.forEach(child => {
              if (child.fields == "reimbursement_grouplist_group_textNote_ordinary") {
                child.name = '报销明细' + (index+1)
              }
            })
          })
        }).catch(res => {})
      }
    }
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/apply';

</style>



