<template>
  <div>
    <div class="createapply-formgroup-title">
      <em v-if="item.is_must==true" class="is-must">*</em> {{item.name}}
    </div>
    <div class="createapply-formgroup-content">
      <div class="select-normal-36">
        <el-select v-model="value" filterable :multiple="item.is_multiple===true" clearable :placeholder="item.placeholder" @change="getData">
          <el-option v-for="(option,oindex) in item.options" :key="oindex" :label="option.name" :value="option.name">
          </el-option>
        </el-select>
      </div>
    </div>
  </div>
</template>
<script>
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
export default {
  data() {
    return {
      value: ''
    }
  },
  components: {},
  props: ['item'],
  created() {
    if (this.item.is_multiple === true) {
      this.value = [];
      this.item.value.forEach(child => {
        this.value.push(child.name)
      })
    } else {
      if (this.item.value.length!=0&&this.item.value[0].name != undefined) {
        this.value = this.item.value[0].name;
      }
    }
  },
  computed: {},
  mounted() {},
  methods: {
    getData() {
      if (this.item.is_multiple === true) {
        this.item.value = this.item.options.filter(op => {
          return this.value.indexOf(op.name) != -1
        })
      } else {
        this.item.value = this.item.options.filter(op => {
          return op.name == this.value
        })
      }
      this.$emit('change',this.item.value);
    },
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/apply';
.fund-items {
  justify-content: space-between;
}

</style>



