<template>
  <div>
    <div v-for="(child,index) in item.children">
      <!-- <em v-if="child.is_must==true" class="is-must">*</em>{{item.name}} -->
      <div v-if="child.fields=='fundSeal_fundRelatePro_fundProject'" class="createapply-formgroup">
        <pro-con-fund :item="child"></pro-con-fund>
      </div>
      <div class="createapply-formgroup" v-if="child.fields=='fundSeal_attachment_file'">
        <upload-file :item="child" type="file"></upload-file>
      </div>
      <div v-if="child.fields=='fundSeal_radio_ordinary'">
        <div class="createapply-formgroup">
          <radio-select :item="child"></radio-select>
        </div>
        <div v-if="child.value[0].name=='是'">
          <div class="createapply-formgroup">
            <input-normal :item="item.children[index+1]"></input-normal>
          </div>
          <!-- 图片 -->
          <div class="createapply-formgroup">
            <upload-file :item="item.children[index+2]" type="photo"></upload-file>
          </div>
        </div>
      </div>
      <!-- 横向页面单选 -->
      <div class="createapply-formgroup" v-if="child.fields=='fundSeal_radio_sms'">
        <radio-select :item="child"></radio-select>
      </div>
    </div>
  </div>
</template>
<script>
//时间范围
import dateRange from '../applyComponent/timeRange.vue'
import uploadFile from '../applyComponent/uploadFile.vue'
import dataSelect from '../applyComponent/dataSelect.vue'
import inputNormal from '../applyComponent/inputNormal.vue'
import radioSelect from '../applyComponent/radioSelect.vue'
//项目关联基金
import proConFund from '../applyComponent/proConFund.vue'
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
export default {
  data() {
    return {}
  },
  components: {
    dateRange,
    uploadFile,
    dataSelect,
    radioSelect,
    inputNormal,
    proConFund
  },
  props: ['item'],
  created() {},
  computed: {},
  mounted() {

  },
  methods: {
    getDate(date) {
      this.item.value = date;
    },
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/apply';
.fund-items {
  justify-content: space-between;
}

</style>



