<template>
  <div>
    <!--container-->
    <!-- <div class="m-t-10">
      <a class="mint-cell mint-field">
        <div class="mint-cell-wrapper">
          <div class="mint-cell-title"> <span class="mint-cell-text">输入地址</span> </div>
          <div class="mint-cell-value">
            <input v-model="address_detail" type="text" id="suggestId" name="address_detail" class="mint-field-core text-right"> </div>
        </div>
      </a>
    </div> -->
    
    <div id="allmap" style="width: 100%;height:600px;border: 1px solid gray;overflow:hidden;"></div>
    <div id="r-result">请输入:
      <input type="text" id="suggestId" size="20" value="百度" style="width:150px;" />
    </div>
    <div id="searchResultPanel" style="border:1px solid #C0C0C0;width:150px;height:auto;"></div>
    <!--container end-->
  </div>
</template>
<script>
export default {
  components: {},
  data: () => ({
    headerData: { title: '办公地址', toLink: '' },
    address_detail: null, // 详细地址    
    userlocation: { lng: '', lat: '' }
  }),
  created() {
    // 组件创建完后获取数据，这里和1.0不一样，改成了这个样子    
    this.loadMap()
    // this.ready() // 如果在此处直接调用this.ready()方法，将无法加载地图  
  },
  mounted() {
    this.ready()
  },
  methods: {
    loadMap: function() {
      // setTimeout(this.ready, 0)    
    },
    ready: function() {

      var th = this
      var map = new BMap.Map('allmap')
      var point = new BMap.Point(114.0552, 22.55) // 创建点坐标     
      map.centerAndZoom(point, 12)
      map.enableScrollWheelZoom() // 启用滚轮放大缩小，默认禁用      
      map.enableContinuousZoom() // 启用地图惯性拖拽，默认禁用      
      map.addControl(new BMap.NavigationControl()) // 添加默认缩放平移控件      
      map.addControl(new BMap.OverviewMapControl()) // 添加默认缩略地图控件      
      map.addControl(new BMap.OverviewMapControl({ isOpen: true, anchor: 'BMAP_ANCHOR_BOTTOM_RIGHT' })) // 右下角，打开  
      var marker = new BMap.Marker(point); // 创建标注    
      map.addOverlay(marker);
      // 建立一个自动完成的对象      
      var ac = new BMap.Autocomplete({ 'input': 'suggestId', 'location': map })
      ac.addEventListener("onhighlight", function(e) { //鼠标放在下拉列表上的事件
        console.log(11);
        var str = "";
        var _value = e.fromitem.value;
        var value = "";
        if (e.fromitem.index > -1) {
          value = _value.province + _value.city + _value.district + _value.street + _value.business;
        }
        str = "FromItem<br />index = " + e.fromitem.index + "<br />value = " + value;

        value = "";
        if (e.toitem.index > -1) {
          _value = e.toitem.value;
          value = _value.province + _value.city + _value.district + _value.street + _value.business;
        }
        str += "<br />ToItem<br />index = " + e.toitem.index + "<br />value = " + value;
        G("searchResultPanel").innerHTML = str;
      });
      var myValue;
      ac.addEventListener("onconfirm", function(e) { //鼠标点击下拉列表后的事件
        console.log(22);
        var _value = e.item.value;
        myValue = _value.province + _value.city + _value.district + _value.street + _value.business;
        G("searchResultPanel").innerHTML = "onconfirm<br />index = " + e.item.index + "<br />myValue = " + myValue;

        setPlace();
      });
      function G(id) {
        return document.getElementById(id);
      }
      function setPlace() {
        map.clearOverlays(); //清除地图上所有覆盖物
        function myFun() {
          var pp = local.getResults().getPoi(0).point; //获取第一个智能搜索的结果
          map.centerAndZoom(pp, 18);
          map.addOverlay(new BMap.Marker(pp)); //添加标注
        }
        var local = new BMap.LocalSearch(map, { //智能搜索
          onSearchComplete: myFun
        });
        local.search(myValue);
      }
    }
  }
}

</script>
<style>
/* 去掉地图左下角的百度LOGO */

.anchorBL {
  display: none
}

</style>



