<template>
  <div>
    <div class="createapply-formgroup-content" v-for="child in curitem.children">
      <div v-if="child.fields=='holiday_textNote_leaveCount'">
        <info-area :item="child"></info-area>
      </div>
      <!-- 单选框 -->
      <div v-if="child.fields=='holiday_select_leaveType'" class="createapply-formgroup">
        <data-select :item="child" @change="undateData"></data-select>
      </div>
      <div v-if="child.fields=='holiday_textNote_leaveResidue'">
        <info-area :item="child"></info-area>
      </div>
      <div v-if="child.fields=='holiday_date_timeRange'" class="createapply-formgroup">
        <date-range :item="child" :type="type"></date-range>
      </div>
      <div v-if="child.fields=='holiday_textNote_ordinary'">
        <info-area :item="child"></info-area>
      </div>
    </div>
  </div>
</template>
<script>
import Bus from '../../config/bus'
//下拉选择框
import dataSelect from '../applyComponent/dataSelect.vue'
//时间范围
import dateRange from '../applyComponent/timeRange.vue'
//说明文字
import infoArea from '../applyComponent/infoArea.vue'
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
export default {
  data() {
    return {
      curitem: '',
      type: {}
    }
  },
  components: {
    dataSelect,
    dateRange,
    infoArea
  },
  props: ['item'],
  created() {
    this.curitem = this.item;
    this.curitem.children.forEach(child => {
      if (child.fields == 'holiday_select_leaveType') {
        if (child.value == '' || child.value == []) {
          this.clearData(false);
        }
      }
    })
    this.item.children = this.curitem.children
  },
  computed: {},
  mounted() {},
  methods: {
    clearData(rel) {
      this.curitem.children.forEach((child, index) => {
        if (child.fields == 'holiday_date_timeRange') {
          if (rel == false) {
            child.value = [];
            child.is_disabled = false;
          } else {
            child.is_disabled = true;
          }
        }
      })
      this.item.children = this.curitem.children;
    },
    undateData(em) {
      if (em == '') {
        this.curitem.children.forEach(child => {
          if (child.fields == 'holiday_date_timeRange') {
            child.value = [];
            this.clearData(false);
            this.type = {};
          }
        })
      } else {
        if (em[0] != undefined && em[0].status != undefined) {
          let datatype = em[0];
          if (datatype.status == 1) {
            this.curitem.children.forEach(child => {
              if (child.fields == 'holiday_date_timeRange') {
                if(child.value[2]!=undefined){

                }
              }
            })
          }
        }
        this.clearData(true);
        this.type = em[0];
      }
      this.item.children = this.curitem.children;
    }
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/apply';

</style>



