<template>
  <div>
    <div class="createapply-formgroup-title">
      <em v-if="item.is_must==true" class="is-must">*</em> {{item.name}}
    </div>
    <div class="createapply-formgroup-content">
      <div class="formgroup-input-box formgroup-money-box">
        <el-input :placeholder="item.placeholder" v-model="item.value[0]" class="input-normal" :clearable='true' @change="checkNum"></el-input>
        <div class="formgroup-money-unit">{{item.unit}}</div>
      </div>
      <div class="formgroup-extra-box" v-if="item.is_extra">
        {{item.extras.name}}：{{item.extras.value}}
      </div>
    </div>
  </div>
</template>
<script>
import {
  dialogConfirm,
  dialogConfirmHasbtn,
  convertCurrency
} from '../../config/mUtils'
export default {
  data() {
    return {
      sealList: [],

    }
  },
  components: {},
  props: ['item'],
  created() {},
  computed: {},
  mounted() {

  },
  methods: {
    checkNum(e) {
      if(e==''){
        this.item.extras.value='';
        return;
      }
      let testnumber = /(^[1-9]([0-9]+)?(\.[0-9]{1,10})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/gi.test(e);
      if (!testnumber) {
        dialogConfirm('info', '输入的格式不正确，请输入整数/小数格式');
        this.item.value=[];
        this.item.extras.value=''
      }else{
        this.item.extras.value=convertCurrency(this.item.value);
      }
      this.$emit('change')
    }
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/apply';
.fund-items {
  justify-content: space-between;
}

</style>



