<template>
  <div>
    <div class="createapply-formgroup-title">
      <em v-if="item.is_must==true" class="is-must">*</em> {{item.name}}
    </div>
    <div class="createapply-formgroup-content">
      <div class="formgroup-radio-box">
        <el-checkbox-group v-model="value" v-on:change='changeRadioshow'>
          <el-checkbox v-for="(check,checkindex) in item.options" :label="check.name" :key="checkindex"></el-checkbox>
        </el-checkbox-group>
      </div>
    </div>
  </div>
</template>
<script>
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
export default {
  data() {
    return {
      value: []
    }
  },
  components: {},
  props: ['item'],
  created() {
    this.value=[];
    this.item.value.forEach(item=>{
      this.value.push(item.name);
    })
  },
  computed: {},
  mounted() {

  },
  methods: {
    changeRadioshow() {
      this.item.value = [];
      this.item.value = this.item.options.filter(item => {
        return this.value.indexOf(item.name)!=-1;
      })
    },
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/apply';
.fund-items {
  justify-content: space-between;
}

</style>



