<template>
  <div>
    <div class="createapply-formgroup-title">
      <em v-if="item.is_must==true" class="is-must">*</em> {{item.name}}
    </div>
    <div class="createapply-formgroup-content">
      <div class="formgroup-radio-box">
        <el-radio-group v-model="value" @change="changeRadioshow()">
          <el-radio v-for="(raido,radioindex) in item.options" :label="raido.name" :key="raido.name">{{raido.name}}</el-radio>
        </el-radio-group>
      </div>
    </div>
  </div>
</template>
<script>
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
export default {
  data() {
    return {
      value:''
    }
  },
  components: {},
  props: ['item'],
  created() {
    this.value=this.item.value[0].name;
  },
  computed: {},
  mounted() {

  },
  methods: {
    changeRadioshow(){
      this.item.value=[];
      this.item.value=this.item.options.filter(item=>{
        return item.name==this.value;
      })
    },
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/apply';
.fund-items {
  justify-content: space-between;
}

</style>



