<template>
  <div>
    <div class="createapply-formgroup-title">
      <em v-if="item.is_must==true" class="is-must">*</em> {{item.name}}
    </div>
    <div class="createapply-formgroup-content">
      <div class="select-normal-36">
        <el-select v-if="item.skip[0].skip_site!=7&&item.skip[0].skip_site!=9" v-model="value" :multiple="item.is_multiple===true" filterable clearable :placeholder="item.placeholder" @change="getSelectdata">
          <el-option v-for="(option,oindex) in options" :key="oindex" :label="option.name" :value="option.id">
          </el-option>
        </el-select>
        <el-select v-else v-model="value" :multiple="item.is_multiple===true" filterable clearable :placeholder="item.placeholder" @change="getSelectCity">
          <el-option-group v-for="(option,oindex) in options" :key="oindex" :label="option.name">
            <el-option v-for="(child,cindex) in option.children" :key="cindex" :label="child.name" :value="child.id">
            </el-option>
          </el-option-group>
        </el-select>
      </div>
    </div>
  </div>
</template>
<script>
import Bus from '../../config/bus'
import { getProlist, getFundlist, getForeignlist, getHolidayslist, getMyholidays, getCityAreaNew, getUserNew, getDepartList, getFundproject, getRelateApprove } from '../../config/api'
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
export default {
  data() {
    return {
      options: [],
      value: '',
      dateList: []
    }
  },
  components: {},
  props: ['item'],
  created() {
    //this.value=this.item.value;
    this.initData();

  },
  computed: {},
  mounted() {

  },
  methods: {
    initData() {
      switch (parseInt(this.item.skip[0].skip_site)) {
        case 1:
          this.getpro();
          break;
        case 2:
          this.getfund();
          break;
        case 3:
          this.getforeign();
          break;
        case 4:
          this.getholidays();
          break;
        case 7:
          this.getCity();
          break;
        case 9:
          this.getUser();
          break;
        case 11:
          this.getDepartment();
          break;
        case 12:
          this.getApprove();
          break;
      }
      
    },
    getSelectdata(e) {
      if (this.item.is_multiple === true) {
        this.item.value = this.options.filter(item => {
          return this.value.indexOf(item.id) != -1
        })
      } else {
        this.item.value = this.options.filter(item => {
          return this.value == item.id
        });
      }
      if (this.item.control == 18 && this.item.is_fresh === true) {
        Bus.$emit('freshApprover', 1, this.value)
      }
      if (this.item.control == 19 && this.item.is_fresh === true) {
        Bus.$emit('freshApprover', 2, this.value)
      }
      this.$emit('change', this.item.value);
    },
    getSelectCity(e) {
      // console.log(this.value);
      if (this.item.is_multiple === true) {
        this.item.value = this.dateList.filter(item => {
          return this.value.indexOf(item.id) != -1
        })
      } else {
        this.item.value = this.dateList.filter(item => {
          return this.value == item.id
        });
      }
    },
    getpro() {
      getProlist().then(res => {
        if (res.data.msgNo == 1) {
          this.options = res.data.payload;
          this.setValue();
        }
      })
    },
    getfund() {
      getFundlist().then(res => {
        if (res.data.msgNo == 1) {
          this.options = res.data.payload;
          this.setValue();
        }
      })
    },
    getforeign() {
      getForeignlist().then(res => {
        if (res.data.msgNo == 1) {
          this.options = res.data.payload;
          this.setValue();
        }
      })
    },
    getholidays() {
      getHolidayslist().then(res => {
        if (res.data.msgNo == 1) {
          this.options = res.data.payload;
          this.setValue();
        }
      })
    },
    getCity() {
      getCityAreaNew().then(res => {
        if (res.data.msgNo == 1) {
          this.options = res.data.payload;
          let dateList = [];
          res.data.payload.forEach(item => {
            if (item.children != undefined) {
              dateList = dateList.concat(item.children);
            }
          })
          this.dateList = dateList;
          this.setValue();
        }
      })
    },
    getUser() {
      getUserNew().then(res => {
        if (res.data.msgNo == 1) {
          this.options = res.data.payload;
          let dateList = [];
          res.data.payload.forEach(item => {
            if (item.children != undefined) {
              dateList = dateList.concat(item.children);
            }
          })
          this.dateList = dateList;
          this.setValue();
        }
      })
    },
    getDepartment() {
      getDepartList().then(res => {
        if (res.data.msgNo == 1) {
          this.options = res.data.payload;
          this.setValue();
        }
      })
    },
    getApprove() {
      getRelateApprove().then(res => {
        if (res.data.msgNo == 1) {
          this.options = res.data.payload;
          this.setValue();
        }
      })
    },
    setValue(){
      if(this.item.is_multiple === true){
        let newArr = [];
        this.item.value.forEach(element => {
          newArr.push(parseInt(element.id));
        });
        this.value = newArr;
      }else{
        if (this.item.value[0] != undefined && this.item.value[0].id != undefined) {
          this.value = parseInt(this.item.value[0].id);
        }
      }
    }
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/apply';

.fund-items {
  justify-content: space-between;
}

</style>



