<template>
  <div class="personal-content-body">
    <div class="content">
      <div class="createapply-group createapply-group-new">
        <div class="createapply-group-title">
          {{applyTypeName}}审批
        </div>
        <div class="clear table-group-title">
          <div class="breadcrumb">
            <el-breadcrumb separator-class="el-icon-arrow-right">
              <el-breadcrumb-item v-for="(item,index) in routeList" :key="index" :to="{name:item.name,query:item.query}">{{item.name_str}}</el-breadcrumb-item>
            </el-breadcrumb>
            <div class="line"></div>
          </div>
        </div>
        <div class="createapply-formbox">
          <div class="createapply-formgroupbox">
            <div v-for="(item,index) in applyShowlist" class="">
              <!-- 单行输入框 -->
              <div class="createapply-formgroup" v-if="item.control==1&&item.is_hidden!==true">
                <input-normal :item="item"></input-normal>
              </div>
              <!-- 多行输入框  -->
              <div class="createapply-formgroup" v-if="item.control==2&&item.is_hidden!==true">
                <input-normal :item="item"></input-normal>
              </div>
              <!-- 数字输入框 -->
              <div class="createapply-formgroup" v-if="item.control==3&&item.is_hidden!==true">
                <number-input :item="item"></number-input>
              </div>
              <!-- 单选框 -->
              <div class="createapply-formgroup" v-if="item.control==4&&item.is_hidden!==true">
                <select-normal :item="item"></select-normal>
              </div>
              <!-- 多选框 -->
              <div class="createapply-formgroup" v-if="item.control==5&&item.is_hidden!==true">
                <select-normal :item="item"></select-normal>
              </div>
              <!-- 日期 -->
              <div class="createapply-formgroup" v-if="item.control==6&&item.is_hidden!==true">
                <date-picker :item="item"></date-picker>
              </div>
              <!-- 图片 -->
              <div class="createapply-formgroup" v-if="item.control==7&&item.is_hidden!==true">
                <upload-file :item="item" type="photo"></upload-file>
              </div>
              <!-- 说明文字 -->
              <div v-if="item.control==8&&item.is_hidden!==true" class="">
                <info-area :item="item"></info-area>
              </div>
              <!-- 金额 -->
              <div class="createapply-formgroup" v-if="item.control==9&&item.is_hidden!==true">
                <money-input :item="item"></money-input>
              </div>
              <!-- 附件 -->
              <div class="createapply-formgroup" v-if="item.control==10&&item.is_hidden!==true">
                <upload-file :item="item" type="file"></upload-file>
              </div>
              <!-- 联系人 -->
              <div class="createapply-formgroup" v-if="item.control==11&&item.is_hidden!==true">
                <data-select :item="item"></data-select>
              </div>
              <!-- 部门 -->
              <div class="createapply-formgroup" v-if="item.control==12&&item.is_hidden!==true">
                <data-select :item="item"></data-select>
              </div>
              <!-- 当前地点 -->
              <div class="createapply-formgroup" v-if="item.control==13&&item.is_hidden!==true">
                <area-input :item="item"></area-input>
              </div>
              <!-- 关联审批单 -->
              <div class="createapply-formgroup" v-if="item.control==14&&item.is_hidden!==true">
                <data-select :item="item"></data-select>
              </div>
              <!-- 用印选择 -->
              <div class="createapply-formgroup" v-if="item.control==15&&item.is_hidden!==true">
                <seal-select :item="item"></seal-select>
              </div>
              <!-- 横向页面单选 -->
              <div class="createapply-formgroup" v-if="item.control==16&&item.is_hidden!==true">
                <radio-select :item="item"></radio-select>
              </div>
              <!-- 横向页面多选 -->
              <div class="createapply-formgroup" v-if="item.control==17&&item.is_hidden!==true">
                <checkbox-select :item="item"></checkbox-select>
              </div>
              <!-- 项目/基金 -->
              <div class="createapply-formgroup" v-if="item.control==18||item.control==19||item.control==20&&item.is_hidden!==true">
                <data-select :item="item"></data-select>
              </div>
              <!-- 项目基金关联 -->
              <div class="createapply-formgroup" v-if="item.control==21&&item.is_hidden!==true">
                <pro-con-fund :item="item"></pro-con-fund>
              </div>
              <!-- 日期区间 -->
              <div v-if="item.control==22&&item.is_hidden!==true" class="createapply-formgroup">
                <date-range :item="item"></date-range>
              </div>
              <!-- 短信通知 -->
              <div class="createapply-formgroup" v-if="item.control==24&&item.is_hidden!==true">
                <radio-select :item="item"></radio-select>
              </div>
              <!-- 城市选择 -->
              <div class="createapply-formgroup" v-if="item.control==25&&item.is_hidden!==true">
                <data-select :item="item"></data-select>
              </div>
              <!-- 请假套件 -->
              <div class="" v-if="item.control==-1&&item.is_hidden!==true">
                <holiday-Control :item="item"></holiday-Control>
              </div>
              <!-- 出差套件 -->
              <div class="" v-if="item.control==-2&&item.is_hidden!==true">
                <bus-trip-control :item="item"></bus-trip-control>
              </div>
              <!-- 外出套件 -->
              <div class="" v-if="item.control==-3&&item.is_hidden!==true">
                <go-out-control :item="item"></go-out-control>
              </div>
              <!-- 报销套件 -->
              <div class="" v-if="item.control==-41&&item.is_hidden!==true">
                <reimbursement-control :item="item"></reimbursement-control>
              </div>
              <!-- 基金用印套件 -->
              <div class="" v-if="item.control==-21&&item.is_hidden!==true">
                <fund-seal-control :item="item"></fund-seal-control>
              </div>
              <!-- 管理公司用印套件 -->
              <div class="" v-if="item.control==-22&&item.is_hidden!==true">
                <manage-seal-control :item="item"></manage-seal-control>
              </div>
              <!-- 管理公司用印套件 -->
              <div class="" v-if="item.control==-23&&item.is_hidden!==true">
                <manage-seal-control :item="item"></manage-seal-control>
              </div>
              <!-- 外资用印套件 -->
              <div class="" v-if="item.control==-23&&item.is_hidden!==true">
                <foreign-seal-control :item="item"></foreign-seal-control>
              </div>
            </div>
          </div>
          <!-- 审批人 -->
          <div class="createapply-formgroup createapply-formgroupbox">
            <div class="createapply-formgroup-title clear">
              审批人
            </div>
            <approver-box :data="approverList"></approver-box>
          </div>
          <!-- 抄送人 -->
          <div class="createapply-formgroup createapply-formgroupbox">
            <div class="createapply-formgroup-title clear">
              抄送人
            </div>
            <div class="createapply-formgroup-content">
              <div class="formgroup-info-box">
                <div><i class="el-icon-info"></i>将在审批完成后进行抄送</div>
              </div>
            </div>
            <copier-box v-if="showCopierInfo" :data="copierList" :default="defaultCopier"></copier-box>
          </div>
          <!-- 提交按钮 -->
          <div class="createapply-btnbox clear">
            <input type="button" class="createapply-btn btn-blue" @click="submitApply" value="提交">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Bus from '../../config/bus'
  //审批人
  import approverBox from '../applyComponent/approverBox.vue'
  //抄送人
  import copierBox from '../applyComponent/copierBox.vue'
  //请假套件
  import holidayControl from '../applyComponent/holidayControl.vue'
  //出差套件
  import busTripControl from '../applyComponent/busTripControl.vue'
  //外出套件
  import goOutControl from '../applyComponent/goOutControl.vue'
  //报销套件
  import reimbursementControl from '../applyComponent/reimbursementControl.vue'
  //基金用印套件
  import fundSealControl from '../applyComponent/fundSealControl.vue'
  //管理公司用印套件
  import manageSealControl from '../applyComponent/manageSealControl.vue'
  //外资用印套件
  import foreignSealControl from '../applyComponent/foreignSealControl.vue'
  //下拉选择框
  import dataSelect from '../applyComponent/dataSelect.vue'
  //说明文字
  import infoArea from '../applyComponent/infoArea.vue'
  //印章选择
  import sealSelect from '../applyComponent/sealSelect.vue'
  //普通选择
  import selectNormal from '../applyComponent/selectNormal.vue'
  //日期选择器
  import datePicker from '../applyComponent/timePicker.vue'
  //项目关联基金
  import proConFund from '../applyComponent/proConFund.vue'
  //时间范围
  import dateRange from '../applyComponent/timeRange.vue'
  //时间范围
  import mapView from '../applyComponent/mapView.vue'
  //数字输入框
  import numberInput from '../applyComponent/numberInput.vue'
  //金额输入框
  import moneyInput from '../applyComponent/moneyInput.vue'
  //radio选择
  import radioSelect from '../applyComponent/radioSelect.vue'
  //checkbox选择
  import checkboxSelect from '../applyComponent/checkboxSelect.vue'
  //输入框
  import inputNormal from '../applyComponent/inputNormal.vue'
  //上传文件
  import uploadFile from '../applyComponent/uploadFile.vue'
  //当前地点
  import areaInput from '../applyComponent/areaInput.vue'
  //审批模拟数据
  import applyData from '../../config/applysetDoc'
  import {
    dialogConfirm,
    compareJson,
    dialogConfirmHasbtn
  } from '../../config/mUtils'
  import {
    getApplyTemp,
    getSpInfoNew,
    submitApply,
    submitEditApply
  } from '../../config/api'
  export default {
    data() {
      return {
        type: 0, //0=>新建1=》编辑，2=〉重新发起
        aid: '',
        tid: 0,
        routeList: [],
        applyShowlist: [], //数据列表
        from: '', //来源2=>项目管理 3=>行政管理
        applyTypeName: '',
        approverList: [], //审批人列表
        showCopierInfo: false,
        copierList: [], //抄送人列表
        defaultCopier: [],
        operatorList: '' //经办人
      }
    },
    watch: {
      // $route(to, from) {
      //   this.initData();
      // }
    },
    components: {
      dateRange,
      datePicker,
      dataSelect,
      inputNormal,
      selectNormal,
      sealSelect,
      radioSelect,
      infoArea,
      checkboxSelect,
      proConFund,
      uploadFile,
      mapView,
      numberInput,
      moneyInput,
      areaInput,
      holidayControl,
      busTripControl,
      goOutControl,
      reimbursementControl,
      fundSealControl,
      manageSealControl,
      approverBox,
      copierBox,
      foreignSealControl
    },
    created() {
      this.routeList = this.$route.meta.routeList;
      //this.applyShowlist = applyData.controlList.concat(applyData.groupControlList);
    },
    computed: {},
    mounted() {
      if (this.$route.query.template_id != undefined) {
        this.tid = this.$route.query.template_id;
      }
      if (this.$route.query.aName != undefined) {
        this.applyTypeName = this.$route.query.aName;
      }
      if (this.$route.query.type != undefined) {
        this.type = this.$route.query.type;
      }
      if (this.$route.query.approval_id != undefined) {
        this.aid = this.$route.query.approval_id;
      }
      this.initData();
      Bus.$on("getCopier", (em) => {
        this.copierList = em;
      })
      Bus.$on("freshApprover", (type, id, id1) => {
        this.updateApproverInfoFun(type, id, id1);
      })
    },
    methods: {
      // beforeRouteEnter(to, from, next) {
      //   next((vm) => {
      //     vm.realList = to.meta.routerList
      //   });
      // },
      //初始化页面
      initData() {
        this.getCreateApplyFun();
        this.getApproverInfoFun();
      },
      //获取审批人和抄送人信息
      getApproverInfoFun() {
        let param = {
          tid: this.tid
        };
        if (this.aid != '') {
          param.aid = this.aid;
        }
        getSpInfoNew(param).then(res => {
          if (res.data.msgNo == 1) {
            this.approverList = res.data.payload.sp;
            this.operatorList = res.data.payload.jr;
            this.copierList = res.data.payload.cs.users;
            this.defaultCopier = res.data.payload.cs.def.toString().split(",");
            this.showCopierInfo = true;
          }
        })
      },
      //刷新审批人
      updateApproverInfoFun(type, id, id1) {
        //type======1=>项目2=>基金
        //id对应type的id
        let param = {
          tid: this.tid
        };
        if (type == 1) {
          param.project_id = id;
        } else if (type == 2) {
          param.fund_id = id;
        } else if (type == 3) {
          param.project_id = id;
          param.fund_id = id1;
        }
        getSpInfoNew(param).then(res => {
          if (res.data.msgNo == 1) {
            this.approverList = res.data.payload.sp;
          }
        })
      },
      //获取用印模板
      getCreateApplyFun() {
        let param = {
          tid: this.tid
        }
        if (this.aid != '') {
          param.aid = this.aid
        }
        getApplyTemp(param).then(res => {
          if (res.data.msgNo == 1) {
            this.applyShowlist = res.data.payload;
            // console.log(this.applyShowlist);
          }
        })
      },
      //提交按钮
      submitApply() {
  
        
        let as = JSON.parse(JSON.stringify(this.applyShowlist));
        // console.log(as);
        // return false;
        let checknull = false;
        checknull = this.cycleCheck(as, checknull);
        if (checknull == true) {
          dialogConfirm('info', '你还有必填项没有填写');
        } else {
          
          // if()

          let canContinue = true //默认可以继续
          let Arr = []; //存放行程时间的数组
          as.forEach((element,index) => {
            if(element.fields=='busTrip'){
              element.children.forEach(element1 => {
                if(element1.fields=='busTrip_grouplist_ordinary'){
                  element1.children.forEach(element2 => {
                    if(element2.fields=='busTrip_grouplist_group_ordinary'){
                      element2.children.forEach(element3 => {
                        if(element3.fields=='busTrip_grouplist_group_dateRange_timeRange'){
                          let newObj = {
                            star: '',
                            end:''
                          }
                          newObj.star = new Date(element3.value[0]).getTime();
                          newObj.end = new Date(element3.value[1]).getTime();
                          Arr.push(newObj);
                        }
                      });
                    }
                  });
                }
              });
            }
          });
          if(Arr.length>0){
            let newArr = Arr.sort(this.compare('star')); //重新按照开始时间排序
            // console.log(newArr);
            // return false;
            //循环比较
            newArr.forEach((element, index) => {
              if (canContinue && index < newArr.length-1) {
                if (element.end > newArr[index+1].star) {
                  canContinue = false
                }
              }
            });
          }
          
          if (!canContinue) {
            dialogConfirm('info', '您的行程时间有冲突');
          } else {
            let cs = {
              def: this.defaultCopier.toString(),
              users: this.copierList
            }
  
            let param = {
              data: JSON.stringify(as),
              sp: JSON.stringify(this.approverList),
              jr: this.operatorList,
              cs: JSON.stringify(cs)
            };
            //type:0,0=>新建1=》编辑，2=〉重新发起
            //修改审批
            if (this.type == 1) {
              param.aid = this.aid;
              submitEditApply(param).then(res => {
                if (res.data.msgNo == 1) {
                  dialogConfirm("success", '提交成功！');
                  let route = {};
                  this.routeList.forEach(item => {
                    if (item.name == 'applyListNew' || item.name == 'applyListNewFirst') {
                      route = item
                    }
                  })
                  route.query.status = 3;
                  this.$router.push({
                    name: route.name,
                    query: route.query
                  })
                } else {
                  // dialogConfirm("fail", '提交失败！')
                  dialogConfirm("fail", res.data.message)
                }
              })
            } else if (this.type == 0) {
              param.tid = this.tid;
              //新建审批
  
              submitApply(param).then(res => {
                if (res.data.msgNo == 1) {
                  dialogConfirm("success", '提交成功！');
                  let route = {};
                  this.routeList.forEach(item => {
                    if (item.name == 'applyListNew' || item.name == 'applyListNewFirst') {
                      route = item
                    }
                  })
                  route.query.status = 3;
                  this.$router.push({
                    name: route.name,
                    query: route.query
                  })
                } else {
                  // dialogConfirm("fail", '提交失败！')
                  dialogConfirm("fail", res.data.message)
                }
              })
            }
          }
        }
      },
      cycleCheck(list, checknull) {
        if (list.control == undefined) {
          for (let i in list) {
            let item = list[i];
            checknull = this.cycleCheck(item, checknull);
            if (checknull) {
              break;
            }
          }
        } else {
          if (list.control > 0) {
            if (this.checkItem(list)) {
              checknull = true;
            }
          } else {
            if (list.children != undefined) {
              let child = list.children;
              for (let j in child) {
                let itemchild = child[j];
                checknull = this.cycleCheck(itemchild, checknull);
                if (checknull) {
                  break;
                }
              }
            }
          }
        }
        return checknull;
      },
      //验证每一项是否完成填写
      checkItem(item) {
        let value = item.value;
        let control = item.control;
        let stringlist = [1, 2, 3, 6, 9]
        let arraylist = [4, 5, 7, 10, 11, 12, 14, 16, 17, 18, 19, 20, 25, 24]
        if (control != 21) {
          if (item.is_must !== true) {
            return false;
          }
        } else {
          if (item.is_must_fund !== true && item.is_must_pro !== true) {
            return false
          }
        }
        if (stringlist.indexOf(control) != -1) {
          if (value[0] == undefined || value[0] === '') {
            return true;
          } else {
            return false;
          }
        } else if (arraylist.indexOf(control) != -1) {
          if (value.length == 0) {
            return true;
          } else {
            return false;
          }
        } else if (control == 13) {
          let obj = value[0]
          let bol = false;
          for (let i in obj) {
            if (obj[i] == '') {
              bol = true;
            }
          }
          return bol;
        } else if (control == 21) { //基金项目关联
          let bol = false;
          if (item.is_must_fund == true) {
            let obj = value[0];
            if(obj){
              for (let i in obj) {
                if (obj[i] == '') {
                  bol = true;
                }
              }
            }else{
              bol = true
            }
            
          }
          if (item.is_must_pro == true) {
            let obj = value[1];
            if(obj){
              for (let i in obj) {
                if (obj[i] == '') {
                  bol = true;
                }
              }
            }else{
              bol = true
            }
          }
          return bol;
        } else if (control == 22) { //日期区间
          let bol = false;
          value.forEach(obj => {
            if (obj == '') {
              bol = true;
            }
          })
          return bol;
        } else if (control == 15) {
          let bol = true;
          value.forEach(obj => {
            delete obj['value!=0']
            let num = parseInt(obj.value)
            if (num != 0) {
              bol = false;
            }
          })
          return bol;
        } else {
          return false;
        }
      },
      //图片获取失败
      getImgurl(e) {
        e.currentTarget.src = '../../static/images/header_userimg.png';
        e.onerror = null;
      },
      //比较
      compare(property) {
        return function(a, b) {
          let value1 = a[property];
          let value2 = b[property];
          return value1 - value2;
        }
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import '../../style/apply';
  .createapply-formbox{
    padding: 10px 20px 100px 20px;
  }
  .table-group-title {
    padding-left: 20px;
  }
  .table-group-title .breadcrumb {
    margin-top: 20px;
  }
  
  .search-box .search-box-input {
    height: 40px;
  }
  
  .search-box .search-box-input:focus {
    background: #fff;
  }
  
  .search-box .search-box-icon {
    top: 12px;
  }
  
  .formgroup-file-item-btn {
    position: relative;
    overflow: hidden;
  }
  
  .formgroup-file-item-btn-inputfile {
    position: absolute;
    left: 0px;
    top: 0px;
    right: 0px;
    bottom: 0px;
    opacity: 0;
    cursor: pointer;
  }
</style>



