<template>
  <div>
    <div class="createapply-formgroup-title">
      <em v-if="item.is_must==true" class="is-must">*</em> {{item.name}}
    </div>
    <div class="createapply-formgroup-content">
      <div class="formgroup-input-box formgroup-money-box">
        <el-input :placeholder="item.placeholder" v-model="item.value[0]" @change="checkNum" class="input-normal" :clearable='true'></el-input>
        <div class="formgroup-money-unit" v-if="item.unit!=''">{{item.unit}}</div>
      </div>
    </div>
  </div>
</template>
<script>
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
export default {
  data() {
    return {
      sealList: [],
    }
  },
  components: {},
  props: ['item'],
  created() {},
  computed: {},
  mounted() {

  },
  methods: {
    checkNum(e) {
      if(e==''){
        return;
      }
      let testnumber = /(^[1-9]([0-9]+)?(\.[0-9]{1,10})?$)|(^(0){1}$)|(^[0-9]\.[0-9]([0-9])?$)/gi.test(e);
      if (!testnumber) {
        dialogConfirm('info', '输入的格式不正确，请输入整数/小数格式');
        this.item.value = [];
      }
    }
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/apply';
.fund-items {
  justify-content: space-between;
}

</style>



