<template>
  <div>
    <div class="createapply-formgroup-title">
      <em v-if="item.is_must==true" class="is-must">*</em>{{item.name}}
    </div>
    <div class="createapply-formgroup-content">
      <div class="formgroup-selectseal-box">
        <div class="formgroup-selectseal-item" v-for="(seal,sealindex) in item.value">
          <el-checkbox class="formgroup-selectseal-item-checkbox left" v-model="seal.value!=0" @change="changeSealCheck($event,seal)">{{seal.name}}</el-checkbox>
          <div class="formgroup-selectseal-item-selectbox">
            数量
            <div class="input-selectbox formgroup-selectseal-item-select" :class="{'input-selectbox-disabled':seal.value==0}">
              <el-select v-model="seal.value" :disabled="seal.value==0" filterable clearable allow-create default-first-option placeholder="请选择">
                <el-option v-for="(item,index) in sealList" :key="item" :label="item" :value="item">
                </el-option>
              </el-select>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
export default {
  data() {
    return {
      sealList: [],
    }
  },
  components: {},
  props: ['item'],
  created() {
    this.initSeallist();
  },
  computed: {},
  mounted() {

  },
  methods: {
    getDate(date) {
      this.item.value = date;
    },
    initSeallist() {
      this.sealList = [];
      for (let i = 1; i < 101; i++) {
        this.sealList.push(i);
      }
    },
    changeSealCheck(e, value) {
      if (e) {
        value.value = 1;
      } else {
        value.value = 0;
      }
    },
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/apply';
.fund-items {
  justify-content: space-between;
}

</style>



