<template>
  <div>
    <div class="createapply-formgroup-title">
      <em v-if="item.is_must==true" class="is-must">*</em> {{item.name}}
    </div>
    <div class="createapply-formgroup-content">
      <div class="select-normal-36">
        <date-picker v-if="item.format=='yyyy-MM-dd HH:mm'||item.format=='yyyy-MM-dd HH:mm:ss'" class="date_picker_normal" :date="date" :option="item.format=='yyyy-MM-dd HH:mm'?option:option1" @change="getDate"></date-picker>
        <el-date-picker v-if="item.format=='yyyy'" :editable='false' v-model="item.value[0]" type="year" placeholder="选择年" :value-format="item.format">
        </el-date-picker>
        <el-date-picker v-if="item.format=='yyyy-MM'" :editable='false' v-model="item.value[0]" type="month" placeholder="选择月" :value-format="item.format">
        </el-date-picker>
        <el-date-picker v-if="item.format=='yyyy-MM-dd'" :editable='false'  v-model="item.value[0]" type="date" placeholder="选择日期" :value-format="item.format">
        </el-date-picker>
      </div>
    </div>
  </div>
</template>
<script>
import myDatepicker from 'vue-datepicker/vue-datepicker-es6-copy.vue'
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
export default {
  data() {
    return {
      date: {
        time: ''
      },
      //时间选择器配置
      option: {
        type: 'min',
        week: ['一', '二', '三', '四', '五', '六', '日'],
        month: ['一月', '二月', '三月', '四月', '五月', '六月', '七月', '八月', '九月', '十月', '十一月', '十二月'],
        format: 'YYYY-MM-DD HH:mm',
        placeholder: '请选择',
        overlayOpacity: 0.5, // 0.5 as default
        dismissible: true // as true as default
      },
      option1: {
        type: 'sec',
        week: ['一', '二', '三', '四', '五', '六', '日'],
        month: ['一月', '二月', '三月', '四月', '五月', '六月', '七月', '八月', '九月', '十月', '十一月', '十二月'],
        format: 'YYYY-MM-DD HH:mm',
        placeholder: '请选择',
        overlayOpacity: 0.5, // 0.5 as default
        dismissible: true // as true as default
      },
    }
  },
  components: {
    'date-picker': myDatepicker,
  },
  props: ['item'],
  created() {
    console.log(this.item);
    // this.item.value = [];
  },
  computed: {},
  mounted() {

  },
  methods: {
    getDate(date) {
      this.item.value.push(date); 
    },
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/apply';
.fund-items {
  justify-content: space-between;
}

</style>



