<template>
  <div>
    <div class="createapply-formgroup-title">
      <em v-if="item.is_must==true" class="is-must">*</em> {{item.name}}
    </div>
    <div class="createapply-formgroup-content" v-if="item.control==7">
      <div class="formgroup-file-box">
        <div class="formgroup-file-item" v-for="(img,imgindex) in value">
          <div class="formgroup-file-item-filename">
            <span>{{img.name}}</span>
          </div>
          <a class="formgroup-file-item-btn">
            <input class="formgroup-file-item-btn-inputfile" type="file" multiple="multiple" accept="image/*" @change="changefile(imgindex,$event)">替换
          </a>
          <span class="formgroup-file-item-btn formgroup-file-item-btn-delete" @click="deletefile(imgindex)">删除</span>
        </div>
      </div>
      <div>
        <el-upload class="upload-demo" drag action="" multiple :auto-upload="true" :show-file-list="false" :http-request="uploadfileRequest" accept="image/*">
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
        </el-upload>
      </div>
    </div>
    
    <div class="createapply-formgroup-content" v-if="item.control==10">
      <div class="formgroup-file-box">
        <div class="formgroup-file-item" v-for="(img,imgindex) in value">
          <div class="formgroup-file-item-filename">
            <span>{{img.name}}</span>
          </div>
          <a class="formgroup-file-item-btn">
            <input class="formgroup-file-item-btn-inputfile" type="file" multiple="multiple" @change="changefile(imgindex,$event)">替换
          </a>
          <span class="formgroup-file-item-btn formgroup-file-item-btn-delete" @click="deletefile(imgindex)">删除</span>
        </div>
      </div>
      <div>
        <el-upload class="upload-demo" drag action="" multiple :auto-upload="true" :show-file-list="false" :http-request="uploadfileRequest">
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
        </el-upload>
      </div>
    </div>
  </div>
</template>
<script>
import myDatepicker from 'vue-datepicker/vue-datepicker-es6-copy.vue'
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
import {
  newapplyUploadFile
} from '../../config/api'
export default {
  data() {
    return {
      delFilelist: [],
      value: []
    }
  },
  components: {},
  props: ['item', 'type'],
  created() {
    if (typeof(this.item.value) != 'object') {
      this.item.value = [];
    } else {
      this.value = this.item.value;
    }
  },
  computed: {},
  mounted() {

  },
  methods: {
    //拖拽上传文件
    uploadfileRequest($event) {
      let file = [];
      var isTrueType = this.getFileType($event.file.name);
      if (!isTrueType) {
        dialogConfirm('info', '不支持该类型文件上传');
        
      } else {
        file.push($event.file);
      }
      this.upload(file);
    },
    upload(file) {
      newapplyUploadFile(file).then(res => {
        if (res.data.msgNo == 1) {
          this.value.push(res.data.payload);
          this.undateData();
        }
      })
    },
    //替换文件
    changefile(index, e) {
      let file = [];
      for (let i = 0; i < e.target.files.length; i++) {
        file.push(e.target.files[i]);
      }
      this.upload(file);
      this.deletefile(index);
    },
    deletefile(index) {
      let imgpath = this.item.value[index].url;
      this.delFilelist.push(imgpath);
      this.value.splice(index, 1);
      this.undateData();
    },
    undateData() {
      this.item.value = this.value;
    },
    //获取文件类型
    getFileType(name) {
      let typelist = ['pdf', 'txt', 'ppt', 'xlsx', 'doc', 'docx', 'pic', 'zip', 'txt', 'rar', 'mp3'];
      let piclist = ['png', 'gif', 'bmp', 'tif', 'tiff', 'jpe', 'jpeg', 'jpg'];
      let pptlist = ['ppt', 'pptx'];
      let doclist = ['doc', 'docx'];
      let xlslist = ['xls', 'xlsx'];
      let ziplist = ['zip', 'rar'];
      let movlist = ['wmv', 'mp4'];
      let txtlist = ['txt', 'xml', 'html', 'css', 'js'];
      let mp3list = ['mp3', 'mp2', 'mpe', 'mpeg', 'mpg'];
	    let type = this.getFileTypeString(name);
	    if (typelist.indexOf(type) != -1) {
	      return true
	    } else if (piclist.indexOf(type) != -1) {
	      return true
	    } else if (pptlist.indexOf(type) != -1) {
	      return true
	    } else if (doclist.indexOf(type) != -1) {
	      return true
	    } else if (xlslist.indexOf(type) != -1) {
	      return true
	    } else if (txtlist.indexOf(type) != -1) {
	      return true
	    } else if (mp3list.indexOf(type) != -1) {
	      return true
	    } else if (ziplist.indexOf(type) != -1) {
	      return true
	    } else if (movlist.indexOf(type) != -1) {
	      return true
	    } else {
	      return false
	    }
    },
     //截取文件后缀名
    getFileTypeString(filename) {
      filename = filename.toString();
      let type = filename.substring(filename.lastIndexOf(".") + 1, filename.length);
      return type;
    },
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/apply';

.fund-items {
  justify-content: space-between;
}

</style>



