<template>
  <div>
    <div class="createapply-formgroup-title">
      <em v-if="item.is_must==true" class="is-must">*</em> {{item.name}}
    </div>
    <div class="createapply-formgroup-content">
      <div class="formgroup-input-box">
        <el-input  :placeholder="item.placeholder" v-model="item.value[0].address" class="input-normal" :clearable='true'></el-input>
      </div>
      <div class="formgroup-extra-box" v-if="item.is_extra">
        {{item.extras.name}}：{{item.extras.value}}
      </div>
    </div>
  </div>
</template>
<script>
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
export default {
  data() {
    return {
      sealList: [],
    }
  },
  components: {},
  props: ['item'],
  created() {
    this.item.extras.value=new Date().Format('yyyy-MM-dd hh:mm:ss')
  },
  computed: {},
  mounted() {

  },
  methods: {
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/apply';
.fund-items {
  justify-content: space-between;
}

</style>



