<template>
  <div>
    <div v-for="child in item.children">
      <div class="createapply-formgroup" v-if="child.fields=='busTrip_text_ordinary'">
        <div class="createapply-formgroup-title">
          <em v-if="child.is_must==true" class="is-must">*</em> {{child.name}}
        </div>
        <div class="createapply-formgroup-content">
          <div class="formgroup-input-box">
            <el-input :placeholder="child.placeholder" v-model="child.value[0]" class="input-normal" :clearable='true'></el-input>
          </div>
        </div>
      </div>
      <div v-if="child.fields=='busTrip_grouplist_ordinary'">
        <div class="createapply-groupbox" v-for="(grouplist,grouplistindex) in child.children">
          <div class="createapply-groupbox-delect">
            <span class="iconfont icon-guanbi" @click="deleteChild(child,grouplistindex)"></span>
          </div>
          <div v-for="(group,groupindex) in grouplist.children">
            <!-- 说明文字 -->
            <div v-if="group.fields=='busTrip_grouplist_group_textNote_ordinary'">
              <div class="createapply-formgroup-content">
                <info-area :item="group"></info-area>
              </div>
            </div>
            <!-- 横向页面多选 -->
            <div class="createapply-formgroup" v-if="group.fields=='busTrip_grouplist_group_checkbox_ordinary'">
              <checkbox-select :item="group"></checkbox-select>
            </div>
            <!-- 横向页面单选 -->
            <div class="createapply-formgroup" v-if="group.fields=='busTrip_grouplist_group_radio_ordinary'">
              <radio-select :item="group"></radio-select>
            </div>
            <!-- 单选框 -->
            <div class="createapply-formgroup" v-if="group.fields=='busTrip_grouplist_group_select_ordinary'">
              <data-select :item="group"></data-select>
            </div>
            <!-- 多选框 -->
            <div class="createapply-formgroup" v-if="group.fields=='busTrip_grouplist_group_multiSelect_ordinary'">
              <data-select :item="group"></data-select>
            </div>
            <div class="createapply-formgroup" v-if="group.fields=='busTrip_grouplist_group_dateRange_timeRange'">
              <date-range :item="group" ></date-range>
              <!-- <date-range :item="group" @change="updateTotal"></date-range> -->
            </div>
          </div>
        </div>
      </div>
      <!-- 说明文字 -->
      <div v-if="child.fields=='busTrip_number_ordinary'" class="createapply-formgroup">
        <div class="createapply-formgroup-content text_center">
          <div class="formgroup-action-box clear" @click="createGroup">
            {{child.name}}
          </div>
        </div>
      </div>
      <!-- 数字输入框 -->
      <div class="createapply-formgroup" v-if="child.fields=='busTrip_number_total'">
        <number-input :item="child"></number-input>
      </div>
      <!-- 多行输入框  -->
      <div class="createapply-formgroup" v-if="child.fields=='busTrip_textarea_ordinary'">
        <input-normal :item="child"></input-normal>
      </div>
      <!-- 联系人 -->
      <div class="createapply-formgroup" v-if="child.fields=='busTrip_innerContact_ordinary'">
        <data-select :item="child"></data-select>
      </div>
    </div>
  </div>
</template>
<script>
import Bus from '../../config/bus'
//下拉选择框
import dataSelect from '../applyComponent/dataSelect.vue'
//时间范围
import dateRange from '../applyComponent/timeRange.vue'
import inputNormal from '../applyComponent/inputNormal.vue'
import numberInput from '../applyComponent/numberInput.vue'
//radio选择
import radioSelect from '../applyComponent/radioSelect.vue'
//checkbox选择
import checkboxSelect from '../applyComponent/checkboxSelect.vue'
//说明文字
import infoArea from '../applyComponent/infoArea.vue'
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
export default {
  data() {
    return {
      curitem: '',
      group: '',
    }
  },
  components: {
    dataSelect,
    dateRange,
    inputNormal,
    numberInput,
    radioSelect,
    checkboxSelect,
    infoArea
  },
  props: ['item'],
  created() {
    this.curitem = this.item;
    this.curitem.children.forEach(item => {
      if (item.fields == 'busTrip_grouplist_ordinary') {
        this.group = JSON.parse(JSON.stringify(item.children[0]));
      }
    })
    this.item.children = this.curitem.children;
  },
  computed: {},
  mounted() {},
  methods: {
    createGroup() {
      this.curitem.children.forEach(item => {
        if (item.fields == 'busTrip_grouplist_ordinary') {
          let group = JSON.parse(JSON.stringify(this.group));
          group.children.forEach(child => {
            if (child.fields == "busTrip_grouplist_group_textNote_ordinary") {
              child.name = '行程' + (item.children.length + 1)
            }
          })
          item.children.push(group);
        }
      })
      this.item.children = this.curitem.children;
    },
    updateTotal() {
      let num = 0;
      this.curitem.children.forEach(item => {
        if (item.fields == 'busTrip_grouplist_ordinary') {
          let children = item.children
          children.forEach(child => {
            child.children.forEach(group => {
              if (group.fields == 'busTrip_grouplist_group_dateRange_timeRange') {
                if (group.value[2] != undefined || group.value[2] != '') {
                  num += group.value[2];
                }
              }
            })
          })
        }
      })
      this.curitem.children.forEach(item => {
        if (item.fields == 'busTrip_number_total') {
          item.value = [];
          item.value.push(num);
        }
      })
      this.item.children = this.curitem.children;
    },
    deleteChild(list, index) {
      if (list.is_delete === true) {
        if (list.children.length == 1) {
          dialogConfirm('info', '至少要有一个行程');
          return;
        }
        dialogConfirmHasbtn('确定要删除该行程吗？').then(res => {
          list.children.splice(index, 1);
          list.children.forEach((group, index) => {
            group.children.forEach(child => {
              if (child.fields == "busTrip_grouplist_group_textNote_ordinary") {
                child.name = '行程' + (index + 1)
              }
            })
          })
          // this.updateTotal();
        }).catch(res => {})
      }
    }
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/apply';

</style>



