<template>
  <div>
    <div v-for="child in curitem.children">
      <div v-if="child.fields=='goOut_selecT_ordinary'&&!child.is_hidden" class="createapply-formgroup">
        <select-normal :item="child" @change="getData"></select-normal>
      </div>
      <div v-if="child.fields=='goOut_dateRange_timeRange'" class="createapply-formgroup">
        <date-range :item="child"></date-range>
      </div>
      <div v-if="child.fields=='goOut_textNote_ordinary'">
        <div class="createapply-formgroup-content">
          <info-area :item="child"></info-area>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
//时间范围
import selectNormal from '../applyComponent/selectNormal.vue'
import dateRange from '../applyComponent/timeRange.vue'
import infoArea from '../applyComponent/infoArea.vue'
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
export default {
  data() {
    return {
      curitem: ''
    }
  },
  components: {
    dateRange,
    selectNormal,
    infoArea
  },
  props: ['item'],
  created() {
    this.curitem = this.item;
    this.curitem.children.forEach(child => {
      if (child.fields == 'goOut_selecT_ordinary') {
        if (child.value == '' || child.value == []) {
          this.clearData(false);
        }
      }
    })
    this.item.children = this.curitem.children
  },
  computed: {},
  mounted() {
  },
  methods: {
    clearData(rel) {
      this.curitem.children.forEach((child, index) => {
        if (child.fields == 'goOut_dateRange_timeRange') {
          if (rel == false) {
            child.value = [];
            child.is_disabled = false;
          } else {
            child.is_disabled = true;
          }
        }
      })
      this.item.children = this.curitem.children;
    },
    getData(em) {
      if (em == '') {
        this.curitem.children.forEach(child => {
          if (child.fields == 'goOut_dateRange_timeRange') {
            child.value = [];
            this.clearData(false);
          }
        })
      } else {
        this.clearData(true);
        this.curitem.children.forEach(child => {
          if (child.fields == 'goOut_dateRange_timeRange') {
            child.value = [];
            child.scal_unit = em[0].scal_unit;
            if (child.scal_unit == 'day') {
              child.format = 'yyyy-MM-dd';
            } else {
              child.format = 'yyyy-MM-dd HH:mm';
            }
          }
        })
      }
      this.item.children = this.curitem.children;
    },
    getDate(date) {
      this.item.value = date;
    },
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/apply';

.fund-items {
  justify-content: space-between;
}

</style>



